import Api from "@/helpers/api";

export default {
  state: {
    isDeliveriesLoading: false,
    isDeliveriesBlocked: false,
    deliveriesData: [],
    isPaymentsLoading: false,
    isPaymentsBlocked: false,
    paymentsData: [],
    orderInfoData: null,
    isOrderInfoLoading: false,
  },
  getters: {
    orderInfo(state) {
      return state.orderInfoData;
    },
    orderInfoLoading(state) {
      return state.isOrderInfoLoading;
    },
    orderInfoProducts(state) {
      return Array.isArray(state?.orderInfoData?.basket?.items)
        ? state.orderInfoData.basket.items.map((product) => ({
            color: product.color.color,
            id: product.product.id,
            price: product.price,
            itemId: product.id,
            amount: product.quantity,
            size: product.size,
            title: product.product.title,
          }))
        : [];
    },
    orderInfoProductsCount(state) {
      return state?.orderInfoData?.basket?.items?.length;
    },
    orderInfoDelivery(state) {
      return state?.orderInfoData?.deliveryType;
    },
    deliveriesLoading(state) {
      return state.isDeliveriesLoading;
    },
    deliveriesBlocked(state) {
      return state.isDeliveriesBlocked;
    },
    deliveries(state) {
      return state.deliveriesData;
    },
    paymentsLoading(state) {
      return state.isPaymentsLoading;
    },
    paymentsBlocked(state) {
      return state.isPaymentsBlocked;
    },
    payments(state) {
      return state.paymentsData;
    },
  },
  mutations: {
    setOrderInfoLoading(state, payload) {
      state.isOrderInfoLoading = payload;
    },
    setOrderInfo(state, payload) {
      state.orderInfoData = payload;
    },
    setIsDeliveriesLoading(state, payload) {
      state.isDeliveriesLoading = payload;
    },
    setIsDeliveriesBlocked(state, payload) {
      state.isDeliveriesBlocked = payload;
    },
    setDeliveries(state, payload) {
      state.deliveriesData = payload;
    },
    setIsPaymentsLoading(state, payload) {
      state.isPaymentsLoading = payload;
    },
    setIsPaymentsBlocked(state, payload) {
      state.isPaymentsBlocked = payload;
    },
    setPayments(state, payload) {
      state.paymentsData = payload;
    },
  },
  actions: {
    async getDeliveries({ commit }, { onErrorCB }) {
      commit("setIsDeliveriesLoading", true);
      commit("setIsDeliveriesBlocked", true);
      try {
        const response = await Api.getDeliveries();
        commit("setDeliveries", response.data);
      } catch (error) {
        commit("setDeliveries", []);
        commit("setPayments", []);
        onErrorCB(error);
      } finally {
        commit("setIsDeliveriesLoading", false);
        commit("setIsDeliveriesBlocked", false);
      }
    },
    async getPaymentsByDelivery({ commit }, { id, onErrorCB }) {
      commit("setIsPaymentsLoading", true);
      commit("setIsPaymentsBlocked", true);
      try {
        const response = await Api.getPaymentsById(id);
        commit("setPayments", response.data);
      } catch (error) {
        commit("setPayments", []);
        onErrorCB(error);
      } finally {
        commit("setIsPaymentsLoading", false);
        commit("setIsPaymentsBlocked", false);
      }
    },

    async getOrderInfoById({ commit }, { id, onErrorCB }) {
      commit("setOrderInfoLoading", true);
      try {
        const response = await Api.getOrderById(id);
        commit("setOrderInfo", response.data);
      } catch (error) {
        onErrorCB(error);
      } finally {
        commit("setOrderInfoLoading", false);
      }
    },
  },
};
